<template>
   <section id="userList">
      <span class="title">{{$t('userList.user')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm" ref="userForm">
            <el-form-item :label="$t('userList.register[0]')">
                <el-date-picker type="date" :placeholder="$t('userList.register[1]')" v-model="userForm.startTime"></el-date-picker>              
            </el-form-item>
            <el-form-item :label="$t('userList.register[2]')">
                <el-date-picker type="date" :placeholder="$t('userList.register[3]')" v-model="userForm.endTime"></el-date-picker>
            </el-form-item>

          <el-form-item>
                <el-input :placeholder="$t('pleaseEnter')" v-model="input" class="input-with-select">
                  <el-select v-model="select" slot="prepend" :placeholder="$t('Pchoose')">
                     <el-option label="UID" value="uid"></el-option>
                     <el-option :label="$t('phone')" value="phoneNumber"></el-option>
                     <!-- <el-option :label="$t('userName')" value="username"></el-option> -->
                     <el-option :label="$t('email')" value="email"></el-option> 
                  </el-select> 
                </el-input>
            </el-form-item>
            <el-form-item>
                 <el-button class="warning" @click="getUsers">{{$t('userList.register[5]')}}</el-button>
            </el-form-item>
            <el-form-item>
                 <el-button class="info"  @click="handleReset('userForm')">{{$t('userList.register[6]')}}</el-button>                 
            </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" @row-click="getUserInfo" >
           <el-table-column prop="uid"  :label="$t('userList.tableHead[0]')" width="175">
            </el-table-column>
            <el-table-column prop="realName" :label="$t('userList.tableHead[1]')">
            </el-table-column>
            <el-table-column prop="cardType"  :label="$t('userList.tableHead[2]')">
            </el-table-column>
            <el-table-column prop="cardNumber"  :label="$t('userList.tableHead[3]')" width="170">
            </el-table-column>
            <el-table-column prop="phoneNumber" :label="$t('userList.tableHead[4]')">
            </el-table-column>
            <el-table-column prop="emailAddress"  :label="$t('userList.tableHead[5]')" width="200">
            </el-table-column>
            <el-table-column prop="ctime" :label="$t('userList.tableHead[6]')" width="160">
            </el-table-column>
            <el-table-column :label="$t('userList.tableHead[7]')">
                <template slot-scope="scope">
                  <el-button size="mini" @click=" pointFix(scope.$index, scope.row)" >{{$t('xiuGai')}}</el-button>     
                </template>
            </el-table-column>
      </el-table>

      <el-dialog :title="$t('fix')" :visible.sync="dialogFormVisible" width="550px">
          <el-form label-position="left" :model="formFix" :rules="ruleFix" ref="formFix">
            <el-form-item>
              <el-row>
                <el-col :span="12"><p class="message">{{$t('userName')}}: <span>{{fixDialog.username}}</span>  </p></el-col>
                <el-col :span="12"> <p class="message"> UID: <span>{{fixDialog.uid}}</span></p></el-col>
              </el-row>
               
             
            </el-form-item>
            <el-form-item :label="$t('phone')"  prop="phoneNumber">
              <el-input v-model="formFix.phoneNumber" :placeholder="$t('Pphone')" ></el-input>
            </el-form-item>
            <el-form-item :label="$t('email')"  prop="email">
              <el-input v-model="formFix.email" :placeholder="$t('email')"></el-input>
            </el-form-item>
            
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="closeFIX" class="exit">{{$t('cancel')}}</el-button>
            <el-button type="primary" @click="updataUser">{{$t('confirm')}}</el-button>
          </div>
      </el-dialog>
      <!-- 分页 -->
      <div class="page" v-show="userTotal>20">
        <el-col :span="24" class="toolbar" >
          <el-pagination background :page-size="20" layout="prev, pager, next" :total="userTotal" @current-change="handleCurrentChange">
          </el-pagination>
        </el-col>
      </div>
     
   </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'
export default {
   data(){
     var checkPhone = (rule,value,callback) =>{
       if(!(/(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/).test(value) && value!="" ){
         callback(new Error("请输入正确的手机号"))
       }else {
         callback()
       }
     };
     var checkEmail = (rule,value,callback) =>{
       if(!(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/).test(value) && value !="" && value != null ){
         callback(new Error("邮箱格式有误"))
       }else{
         callback()
       }
     };
     return{
       userList:[],
       page:1,
       userTotal:0,
       formFix: {
          uid:"",
          phoneNumber: '',
          email: '',
        },
        ruleFix:{
          phoneNumber:[
            {validator:checkPhone,trigger:'change'}
            ],
          email:[
            {validator:checkEmail,trigger:'change'}
          ]
        },
       userForm:{
         startTime:'',
         endTime:'',
         page:1,
         rowNum:"20",
       },
       startT:"",
       endT:"",
       input:"",
       select:"",
       userLoading:false,
       dialogFormVisible: false,
       fixDialog:{
        uid:"",
        username:"",
       },
     }
   },
   methods:{
     /**
      * [getUsers description]
      * 用户管理查询
      * ------------------------------
      * @return {[type]} [description]
      */
    
     getUsers:function(){
         if(this.select != ""){
            this.userForm[this.select] = this.input;
         }
         if(this.userForm.startTime != ""){
          //  this.userForm.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
          let startTimeC = dateStr2GmtTimeStamp(this.userForm.startTime);
           // 获取秒的时间戳用于给后台传数据
          //  this.startT = dateStrSecond(this.userForm.startTime)
           this.startT = dateStrSecond(startTimeC)
         }
         if(this.userForm.endTime != ""){
          //  this.userForm.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
           let endTimeC = dateStr2GmtTimeStamp(this.userForm.endTime);
          //  this.endT=dateStrSecond(this.userForm.endTime)
           this.endT=dateStrSecond(endTimeC)
         }
         this.userLoading = true;
        // console.log(this.ApiConfig.userManagement.queryFrontUserInfo)
        //  console.log(this.userForm)

         let  data = {
          page:this.userForm.page,
          rowNum:this.userForm.rowNum,
          uid:"",
          phoneNumber:"",
          username:"",
          email:"",
          startTime:this.startT,
          endTime:this.endT,
         }
         if(this.select=="uid"){
          data.uid = this.input;
          // console.log(data.uid)
          
         }else if(this.select=="phoneNumber"){
          data.phoneNumber = this.input;
          // console.log(data.phoneNumber)
          
         }else if(this.select == "username"){
          data.username = this.input;
          // console.log(data.username)
          
        }else if(this.select=="email"){
          data.email = this.input;
          // console.log(data.email)
        }
        // console.log("请求参数丢失问题解决")
        // console.log(data)
         ajax(this,this.ApiConfig.userManagement.queryFrontUserInfo,data,(res) => {
             if(res.code==200){
                this.userList = res.data.rows;
                // console.log(res)
                // console.log(this.$store.state.lang)
                this.userTotal = res.data.total;
                let arr = this.userList;
                arr.forEach((item,i,arr) => {
                   this.userList[i].ctime = getDataYear('Y-M-D h:m:s',arr[i].ctime);
                   this.userList[i].utime = getDataYear('Y-M-D h:m:s',arr[i].utime);
                   if(this.userList[i].cardType==1){
                    this.userList[i].cardType="中国身份证"
                   }else if(this.userList[i].cardType==2){
                    this.userList[i].cardType="外国证件"
                   }
                });
                this.startT = "";
                this.endT = "";
                this.userLoading = false;
             }else {
              this.userLoading = false;
                 this.$notify({
                    title:"提示",
                    message:res.message,
                    type:"warning"
                 })
                 return false;
             }
         },"POST",this.$store.state.lang)
     },
     //点击修改 需拿的一些信息 弹框显示
     pointFix(index,row){
        this.dialogFormVisible = true;
        this.formFix.phoneNumber = row.phoneNumber;
        this.formFix.email = row.emailAddress;
     },
     closeFIX:function(){
      this.formFix.phoneNumber="";
      this.formFix.email="";
      this.dialogFormVisible = false;
       
     },

     // 修改用户信息
     updataUser:function(){
            
            // console.log("修改成功你却不变?")
            // console.log(this.formFix)
            if((this.formFix.phoneNumber == "" || this.formFix.phoneNumber == null)  && (this.formFix.email =="" ||this.formFix.email==null) ){
              this.$notify({
                title:"提示",
                message:"邮箱和电话号码不能都为空",
                type:"warning"
              })
              return false;
            }else{

              
            this.formFix.uid = this.fixDialog.uid ;
 this.$refs.formFix.validate((valid) => {
          if (valid) {
              ajax(this,this.ApiConfig.userManagement.updateFrontUserInfo,this.formFix,(res) => {
                this.$notify({
                    title:"提示",
                    message:res.message,
                    type:"warning"})

                this.dialogFormVisible = false;

              this.getUsers();
              
            },"POST")
      } else{
           
            return false;
          }
        });


            }
            
     },
     /**
      * [handleCurrentChange description]
      *  分页选择
      *  --------------------------------
      * @param  {[type]} val [description]  当前选中页
      * @return {[type]}     [description]
      */
     handleCurrentChange:function(val){
       this.userForm.page = val;
       this.getUsers();
     },
     /**
      * [handleReset description]
      *  重置筛选
      *  ----------------------------
      * @return {[type]} [description]
      */
     handleReset:function(forName){

       this.$refs[forName].resetFields();
       /*    startTime:'',
         endTime:'',
         page:1,
         rowNum:"10", */
      //  this.userForm ={};
       this.userForm.startTime="";
       this.userForm.endTime="";
       this.userForm.page="1";
       this.userForm.rowNum=20;

       this.select="";
       this.input = "";
       this.getUsers();
     },
     /**
      * [tableRowClassName description]
      * 行的 className 的回调方法，也可以使用字符串为所有行设置一个固定的 className
      * -----------------------------------------------
      * @param  {[type]} options.row      [description] 
      * @param  {[type]} options.rowIndex [description] 行
      * @return {[type]}                  [description]
      */
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
    /*
      [getUserInfo description]

     *获取表格点击行的信息并赋值给弹框中对应的信息*/
    getUserInfo:function(row,event,column){
        // console.log(row)
        this.fixDialog.uid = row.uid;
        this.fixDialog.username = row.realName;
        },

   },


   mounted:function() {
     this.getUsers();
   },
}
</script>
<style scoped lang="postcss" > 
  .message{
    // width: 313px;
    height: 20px;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    color: #a4a4a4;
    // background: red;
  }
</style>
<style>
#userList {
  .el-dialog__title{
  width: 81px;
  height: 20px;
  font-size: 20px;
  font-weight: normal;
  font-stretch: normal;
  color: #333333;

 }
 .el-dialog__header{
  padding-bottom: 0;
 }
 .el-dialog__body{
  padding-top: 0;
 }
 .dialog-footer{
  margin: 0 auto;
  width: 60%;
  display: flex;
  justify-content: space-around;
 }
 .el-select .el-input {
    width: 105px;
  }
  .dialog-footer{
    .exit{
       background-color: #f0f0f0;
    }
  }
  
}
</style>


